/* 전체 기본 설정 */
body {
  margin: 0;
  font-family: Arial, sans-serif;
  background: #ffffff;
}

/* -------- 홈(랜딩) 페이지 전용 스타일 -------- */

.home-page {
  background: #ffffff;
}

/* 각 섹션 공통 패딩 (여백 넉넉하게) */
.home-section {
  padding: 96px 24px;
}

.home-inner {
  max-width: 960px;
  margin: 0 auto;
}

/* 1. 첫 화면 히어로 섹션 (정중앙 배치) */
.hero-section {
  padding-top: 60px;
  padding-bottom: 80px;
}

.hero-center-inner {
  min-height: calc(100vh - 160px); /* 헤더+푸터 제외 대략 값 */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.hero-title {
  font-size: 40px;
  font-weight: 700;
  margin-bottom: 16px;
  line-height: 1.3;
}

.hero-highlight {
  color: #0d47a1;
}

.hero-subtitle {
  font-size: 18px;
  color: #444;
  margin-bottom: 24px;
  line-height: 1.7;
}

/* 히어로 버튼 */
.hero-buttons {
  margin-top: 8px;
  justify-content: center;
}

/* 백엔드 상태 영역 */
.backend-status-box {
  margin-top: 24px;
}

.backend-status-text {
  font-size: 13px;
  color: #555;
  background: #f5f5f5;
  display: inline-block;
  padding: 6px 10px;
  border-radius: 999px;
}

.backend-status-text span {
  color: #0077ff;
}

/* 2. 가운데 정렬 섹션들 */
.section-center {
  text-align: center;
}

.section-title {
  font-size: 32px;
  font-weight: 700;
  margin-bottom: 16px;
}

.section-text {
  font-size: 16px;
  color: #444;
  margin-bottom: 20px;
  line-height: 1.8;
}

/* 배경 색이 살짝 다른 섹션 */
.home-section-alt {
  background: #f7fbff;
}

/* 설명 리스트 */
.section-list {
  margin: 0;
  padding-left: 0;
  list-style: none;
  font-size: 15px;
  color: #333;
  line-height: 1.7;
}

.section-list li {
  margin-bottom: 8px;
}

/* 기능 카드 그리드 */
.feature-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  gap: 24px;
  margin-top: 24px;
}

.feature-card {
  background: #ffffff;
  border-radius: 16px;
  padding: 20px 18px;
  box-shadow: 0 10px 24px rgba(0, 0, 0, 0.04);
  border: 1px solid #e0e0e0;
  font-size: 14px;
  line-height: 1.6;
}

.feature-card h3 {
  margin-top: 0;
  margin-bottom: 10px;
  font-size: 17px;
}

/* CTA 섹션 중앙 정렬 */
.home-section-cta {
  background: #f1f8e9;
}

.cta-inner {
  text-align: center;
}

.cta-buttons {
  justify-content: center;
}

/* 메인 버튼 강조 */
.hero-main-button {
  min-width: 230px;
  font-size: 15px;
}

/* -------- 공통 페이지 스타일 (다른 페이지에서 사용) -------- */

.page {
  max-width: 800px;
  margin: 0 auto;
  padding: 24px;
  font-family: Arial, sans-serif;
}

.page h1 {
  margin-bottom: 8px;
}

.sub-text {
  margin-bottom: 16px;
  color: #555;
}

/* 백엔드 상태 박스 (다른 페이지에서 쓸 수도 있음) */
.status-box {
  display: inline-flex;
  align-items: center;
  padding: 8px 12px;
  border-radius: 6px;
  background: #f5f5f5;
  margin-bottom: 20px;
}

.status-label {
  font-weight: bold;
  margin-right: 8px;
}

.status-value {
  color: #0077ff;
}

/* 버튼 공통 */
.button-row {
  display: flex;
  gap: 12px;
  margin-top: 16px;
  flex-wrap: wrap;
}

.button {
  display: inline-block;
  margin-top: 12px;
  padding: 12px 20px;
  background: #0077ff;
  color: white;
  border-radius: 999px;
  text-decoration: none;
  border: none;
  cursor: pointer;
  font-size: 15px;
}

.button:hover {
  background: #005fcc;
}

.button.secondary {
  background: #ffffff;
  color: #0077ff;
  border: 1px solid #0077ff;
}

.button.secondary:hover {
  background: #e3f2fd;
}

/* 입력/셀렉트 공통 */
input,
select {
  display: block;
  margin-top: 10px;
  margin-bottom: 8px;
  padding: 8px;
  width: 220px;
}

/* 카드 박스 (추천 결과, 상세 설명 등) */
.card {
  border-radius: 8px;
  padding: 16px;
  background: #f8f9ff;
  margin-bottom: 16px;
}

/* 추천 이력 리스트 */
.history-list {
  list-style: none;
  padding: 0;
  margin: 16px 0;
}

.history-item {
  border-radius: 8px;
  border: 1px solid #ddd;
  padding: 12px 16px;
  margin-bottom: 12px;
  background: #ffffff;
}

.history-header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 8px;
}

.history-cert {
  font-weight: bold;
}

.history-date {
  font-size: 12px;
  color: #777;
}

.history-body p {
  margin: 4px 0;
}

.history-message {
  margin-top: 4px;
  color: #333;
}

.empty-text {
  color: #777;
  margin: 16px 0;
}

/* 링크 공통 */
.link-row {
  margin-top: 16px;
}

.link {
  color: #0077ff;
  text-decoration: none;
  font-size: 14px;
}

.link:hover {
  text-decoration: underline;
}

/* 성공 메시지 */
.success-text {
  margin-top: 10px;
  color: #0c8a2b;
  font-size: 14px;
}

/* 자격증 상세 페이지 팁 리스트 */
.tip-list {
  margin: 8px 0 16px;
  padding-left: 20px;
}

.tip-item {
  margin-bottom: 4px;
}

/* 에러 텍스트 (GoalSettingPage 등) */
.error-text {
  margin-top: 2px;
  margin-bottom: 6px;
  font-size: 13px;
  color: #d32f2f;
}

/* 로그인/회원가입 페이지 레이아웃 */
.auth-page {
  min-height: calc(100vh - 140px);
  display: flex;
  align-items: center;
  justify-content: center;
}

.auth-card {
  width: 100%;
  max-width: 420px;
  padding: 32px 28px;
  border-radius: 16px;
  background: #ffffffdd;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.08);
}

.auth-title {
  font-size: 24px;
  margin-bottom: 4px;
}

.auth-subtitle {
  font-size: 14px;
  color: #666;
  margin-bottom: 20px;
}

.auth-button {
  width: 100%;
  margin-top: 16px;
  text-align: center;
}

.auth-footer-text {
  margin-top: 12px;
  font-size: 13px;
  color: #555;
}

/* 반응형: 화면이 좁을 때 여백/폰트 조정 */
@media (max-width: 768px) {
  .home-section {
    padding: 64px 16px;
  }

  .hero-center-inner {
    min-height: calc(100vh - 140px);
  }

  .hero-title {
    font-size: 30px;
  }

  .section-title {
    font-size: 24px;
  }
}

/* 2번 섹션: 텍스트 왼쪽 + 이미지 오른쪽 레이아웃 */
.info-layout {
  display: flex;
  align-items: center;
  gap: 48px;
}

.info-text {
  flex: 1;
  text-align: left;
}

.info-image {
  flex: 1;
  display: flex;
  justify-content: center;
}

.info-image img {
  max-width: 360px;
  width: 100%;
  height: auto;
  object-fit: contain;
}

/* 화면이 좁을 때는 위/아래로 쌓이게 */
@media (max-width: 768px) {
  .info-layout {
    flex-direction: column;
    text-align: center;
  }

  .info-text {
    text-align: center;
  }
}
/* 회사 로고 섹션 */
.company-section {
  background: #ffffff;
}

.company-inner {
  text-align: center;
}

.company-text {
  max-width: 720px;
  margin: 0 auto 32px;
}

/* 10개씩 3줄 느낌으로 로고 배치 */
.company-grid {
  display: grid;
  grid-template-columns: repeat(10, minmax(60px, 1fr));
  gap: 18px 24px;
  justify-items: center;
}

.company-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 6px;
}

.company-item img {
  max-width: 60px;   /* 로고 너무 크지 않게 */
  max-height: 40px;
  object-fit: contain;
  filter: grayscale(0.1);
  opacity: 0.95;
}

.company-name {
  font-size: 11px;
  color: #555;
}

/* 로고 섹션 반응형 */
@media (max-width: 1024px) {
  .company-grid {
    grid-template-columns: repeat(6, minmax(70px, 1fr));
  }
}

@media (max-width: 600px) {
  .company-grid {
    grid-template-columns: repeat(3, minmax(80px, 1fr));
  }

  .company-item img {
    max-width: 54px;
  }
}

/* 스크롤 등장 애니메이션 공통 */
.reveal-on-scroll {
  opacity: 0;
  transform: translateY(24px);
  transition: opacity 0.6s ease, transform 0.6s ease;
}

.reveal-visible {
  opacity: 1;
  transform: translateY(0);
}

/* 숫자 카운트 섹션 스타일 */
.stats-section {
  background: #f8fafc;
}

.stats-inner {
  text-align: center;
}

.stats-grid {
  display: flex;
  flex-wrap: wrap;
  gap: 24px;
  justify-content: center;
  margin-top: 24px;
}

.stat-item {
  min-width: 180px;
  padding: 16px 20px;
  border-radius: 12px;
  background: #ffffff;
  box-shadow: 0 4px 16px rgba(15, 23, 42, 0.06);
  display: flex;
  flex-direction: column;
  align-items: center;
}

.stat-label {
  font-size: 13px;
  color: #64748b;
  margin-bottom: 6px;
}

.stat-number {
  font-size: 26px;
  font-weight: 700;
  color: #0f172a;
}

/* 레이아웃/헤더 스크롤 스타일 */
.app-shell {
}

.site-main {
}

/* 헤더를 감싸는 래퍼 */
.header-wrapper {
  position: sticky;
  top: 0;
  z-index: 50;
  background: transparent;
  transition: background-color 0.2s ease, box-shadow 0.2s ease,
    border-bottom 0.2s ease;
}

/* 스크롤 되었을 때 헤더 배경/그림자 */
.header-scrolled {
  background: #ffffff;
  box-shadow: 0 4px 16px rgba(15, 23, 42, 0.06);
  border-bottom: 1px solid #e2e8f0;
}


/* ============================= */
/* 다크/라이트 테마 공통 스타일  */
/* ============================= */

html.theme-light body {
  background-color: #f8fafc;
  color: #0f172a;
}

html.theme-dark body {
  background-color: #020617;
  color: #e5e7eb;
}

/* 다크 모드에서 카드/통계 박스 등 살짝 어둡게 */
html.theme-dark .feature-card,
html.theme-dark .stat-item,
html.theme-dark .company-item {
  background: #0f172a;
  color: #e5e7eb;
}

/* 다크 모드에서 CTA, 히어로 섹션 느낌 */
html.theme-dark .hero-section {
  background: radial-gradient(circle at top, #1e293b 0, #020617 60%);
}

html.theme-dark .home-section-cta {
  background: #0b1120;
}

/* 다크 모드에서 텍스트 살짝 밝게 */
html.theme-dark .section-title {
  color: #e5e7eb;
}

html.theme-dark .section-text,
html.theme-dark .company-text,
html.theme-dark .last-visit-text {
  color: #cbd5f5;
}

/* 헤더를 감싸는 래퍼 (스크롤 시 배경/그림자) */
.header-wrapper {
  position: sticky;
  top: 0;
  z-index: 50;
  background: transparent;
  transition: background-color 0.2s ease, box-shadow 0.2s ease,
    border-bottom 0.2s ease;
}

.header-scrolled {
  background: #ffffff;
  box-shadow: 0 4px 16px rgba(15, 23, 42, 0.06);
  border-bottom: 1px solid #e2e8f0;
}

/* 다크 모드일 때 헤더도 살짝 어둡게 */
html.theme-dark .header-scrolled {
  background: #020617;
  border-bottom-color: #1f2937;
}

/* 다크 모드 토글 버튼: 화면 왼쪽 아래 고정 */
.theme-toggle-btn {
  position: fixed;
  left: 24px;
  bottom: 24px;
  padding: 8px 14px;
  font-size: 13px;
  border-radius: 999px;
  border: 1px solid #cbd5f5;
  background: #ffffff;
  color: #0f172a;
  cursor: pointer;
  box-shadow: 0 8px 20px rgba(15, 23, 42, 0.25);
  opacity: 0.95;
  z-index: 90;
}

.theme-toggle-btn:hover {
  opacity: 1;
  transform: translateY(-1px);
}

html.theme-dark .theme-toggle-btn {
  background: #020617;
  color: #e5e7eb;
  border-color: #334155;
}

/* 맨 위로 이동 버튼: 오른쪽 아래 고정 */
.scroll-top-button {
  position: fixed;
  right: 24px;
  bottom: 24px;
  padding: 10px 14px;
  border-radius: 999px;
  border: none;
  background: #0f172a;
  color: #f9fafb;
  font-size: 13px;
  cursor: pointer;
  box-shadow: 0 8px 24px rgba(15, 23, 42, 0.5);
  opacity: 0.9;
  z-index: 80;
  transition: opacity 0.15s ease, transform 0.15s ease;
}

.scroll-top-button:hover {
  opacity: 1;
  transform: translateY(-1px);
}


/* ============================== */
/* 다크 모드 하단 섹션/카드 보정 */
/* ============================== */

/* 다크 모드 기본 배경 + 글자 색 */
html.theme-dark body {
  background-color: #020617;
  color: #e5e7eb;
}

/* 섹션 배경을 어두운 톤으로 통일 */
html.theme-dark .home-section,
html.theme-dark .home-section-alt,
html.theme-dark .stats-section,
html.theme-dark .company-section {
  background: #020617;
}

/* 히어로 / CTA 섹션도 다크 분위기 맞추기 */
html.theme-dark .hero-section {
  background: radial-gradient(circle at top, #1e293b 0, #020617 60%);
}

html.theme-dark .home-section-cta {
  background: #020617;
}

/* 카드류(기능 카드, 통계 카드, 회사 로고 카드) */
html.theme-dark .feature-card,
html.theme-dark .stat-item,
html.theme-dark .company-item {
  background: #0b1120;
  border: 1px solid #1f2937;
  color: #e5e7eb;
}

/* 텍스트 톤 보정 */
html.theme-dark .section-title {
  color: #e5e7eb;
}

html.theme-dark .section-text,
html.theme-dark .company-text,
html.theme-dark .last-visit-text {
  color: #cbd5f5;
}

/* 백엔드 상태 / 마지막 방문 박스 */
html.theme-dark .backend-status-box,
html.theme-dark .visit-info-box {
  background: #0b1120;
  color: #e5e7eb;
}

/* 버튼 색 보정 */
html.theme-dark .hero-main-button {
  background: #3b82f6;
  color: #f9fafb;
}

html.theme-dark .button.secondary {
  background: #111827;
  color: #e5e7eb;
}

/* 헤더가 스크롤 되었을 때 어두운 톤 적용 */
html.theme-dark .header-scrolled {
  background: #020617;
  border-bottom-color: #1f2937;
  box-shadow: 0 4px 16px rgba(15, 23, 42, 0.7);
}

/* 다크 모드 토글 버튼 자체도 어두운 스타일 */
html.theme-dark .theme-toggle-btn {
  background: #020617;
  color: #e5e7eb;
  border-color: #334155;
}
